import { useState, useEffect, useRef } from "react";
import * as moviesApi from "../services/moviesApi";
import {
  useParams,
  useHistory,
  NavLink,
  useRouteMatch,
  Switch,
  Route,
} from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Cast from "../views/Cast";
import Reviews from "../views/Reviews";

export default function MovieDetailsPage() {
  const { movieId } = useParams();
  const [movies, setMovies] = useState(null);

  const history = useHistory();
  const backLocation = useRef();
  const { url } = useRouteMatch();

  useEffect(() => {
    function getMovie() {
      try {
        moviesApi.fetchMoviesDetails(movieId).then(setMovies);
      } catch (error) {
        if (error.response) {
          toast.warning("Page not found!");
        }
      }
    }
    getMovie();
  }, [movieId]);
  const onBackClick = () => {
    history.push(backLocation?.current?.state?.from ?? "/");
  };
  return (
    movies && (
      <div>
        <button type="button" onClick={onBackClick}>
          Go back
        </button>
        <div>
          <div>
            <img
              src={`https://image.tmdb.org/t/p/w300${movies.poster_path}`}
              alt={movies.original_title}
            />
          </div>
          <div>
            <h2>{movies.original_title}</h2>
            <p>Author: {movies.author}</p>
            <h3>Overview</h3>
            <p>{movies.overview}</p>
            <h4>Genres</h4>
            <p>
              {movies.genres.map((genre) => (
                <span key={genre.id}>{genre.name}</span>
              ))}
            </p>
          </div>
        </div>
        <p>Aditional information</p>
        <NavLink to={`${url}/reviews`}>Reviews</NavLink>
        <NavLink to={`${url}/cast`}>Cast</NavLink>
        <Switch>
          <Route path="/movies/:movieId/cast" component={Cast}></Route>
          <Route path="/movies/:movieId/reviews" component={Reviews}></Route>
        </Switch>
      </div>
    )
  );
}
